// Typography
//––––––––––––––––––––––––––––––––––––––––––––––––––

@import 'type/fonts';

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 2rem;
  font-weight: $font-w-bold;
  font-family: $font-heading;
}

h1 { @include font-size($font-s-h1); line-height: 1.2;  letter-spacing: -.1rem;  }
h2 { @include font-size($font-s-h2); line-height: 1.25; letter-spacing: -.1rem;  }
h3 { @include font-size($font-s-h3); line-height: 1.3;  letter-spacing: -.1rem;  }
h4 { @include font-size($font-s-h4); line-height: 1.35; letter-spacing: .04rem; }
h5 { @include font-size($font-s-h5); line-height: 1.5;  letter-spacing: -.05rem; }
h6 { @include font-size($font-s-h6); line-height: 1.6;  letter-spacing: 0;       }

// use @include media();
@include media('>tablet') {
  h1 { @include font-size($font-s-h1 * 1.5); }
  h2 { @include font-size($font-s-h2 * 1.5); }
  h3 { @include font-size($font-s-h3 * 1.5); }
  h4 { @include font-size($font-s-h4 * 1.5); }
  h5 { @include font-size($font-s-h5 * 1.5); }
  h6 { @include font-size($font-s-h6 * 1.5); }
}

// classes for all heading types across the site
@import 'type/headings';

// stylings for content sections and long-form text
@import 'type/content';

// atomic rules for specific styling
@import 'type/atomic';

// UI styles, numbers, forms, and other elements
@import 'type/styles';
